// Define a function to create multiple box-shadows
@function multiple-box-shadow($n) {
  $value: '#{random(2000)}px #{random(2000)}px #FFF';
  @for $i from 2 through $n {
    $value: '#{$value}, #{random(2000)}px #{random(2000)}px #FFF';
  }
  @return unquote($value);
}

// Use the function to create shadows for different layers
$shadows-small:  multiple-box-shadow(700);
$shadows-medium: multiple-box-shadow(200);
$shadows-big:    multiple-box-shadow(100);

// Starry background styles scoped under .dark-mode .home
body .dark-mode .home {
  position: relative;
  z-index: 1;
  background: radial-gradient(ellipse at bottom, #1B2735 0%, #090A0F 100%) !important;
  overflow: hidden;

  // Apply the starry background styles 
  &:before {
    content: " ";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    display: block;
    background: radial-gradient(ellipse at bottom, #1B2735 0%, #090A0F 100%);
    overflow: hidden;

  html body .dark-mode div#home #stars,
  html body .dark-mode div#home #stars2,
  html body .dark-mode div#home #stars3 {
      width: 1px;
      height: 1px;
      background: transparent;
    }

    #stars {
      box-shadow: $shadows-small;
      animation: animStar 50s linear infinite;
    }

    #stars2 {
      width: 2px;
      height: 2px;
      box-shadow: $shadows-medium;
      animation: animStar 100s linear infinite;
    }

    #stars3 {
      width: 3px;
      height: 3px;
      box-shadow: $shadows-big;
      animation: animStar 150s linear infinite;
    }
  }
}

#title {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  color: #FFF;
  text-align: center;
  font-family: 'Lato', sans-serif;
  font-weight: 300;
  font-size: 50px;
  letter-spacing: 10px;
  margin-top: -60px;
  padding-left: 10px;

  span {
    background: linear-gradient(white, #38495a);
    -webkit-background-clip: text;
    color: transparent;
  }
}

// Define the keyframes for the animation
@keyframes animStar {
  from {
    transform: translateY(0px);
  }
  to {
    transform: translateY(-2000px);
  }
}
